export enum StyleInputType {
    // 输入框
    INPUT,
    // 图片链接或上传图片
    INPUTUPLOAD,
    // 选择器
    SELECT,
    // 上传文件
    UPLOAD,
    // 多个配置 如 border -> [boderleft,....]
    ARRAY,
    // box-shadow: [xxx,xxx,xxx]
    ATTRARRAY,
    // 取色器
    COLORPICKER,
    // boolean 选择
    SWITCH,
    // 类似于 word 的样式按钮
    BUTTONSWITCH,
    SILDER
}
